.feedback {
  background-color: #e77823;
}

.feedback__container {
  width: 100%;
  justify-content: center;
  display: flex;
}

.feedback__form {
  width: 480px;
  background-color: #edd;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 5px;
  flex-direction: column;
  margin-top: 70px;
  padding: 12px;
  display: flex;
}

.feedback__form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback__form button {
  font: inherit;
  cursor: pointer;
  background-color: #a39a9a;
  border-radius: 4px;
  padding: 8px 12px;
}

.feedback__form button:hover, .feedback__form button:focus {
  background-color: #c0b4b4;
}

input, textarea {
  font: inherit;
  background-color: #f7eded;
  padding: 8px;
}

textarea {
  resize: none;
}

